.maincontainer {
  justify-content: center;
}

.margin {
  margin-left: 10px;
  background-color: #333c56;
  border-radius: 100px;
  padding: 30px;
  width: 100%;
  height: 5vh;
  color: white;
}

.form {
  display: flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
}

.grid {
  display: flex;
  justify-content: right;
  align-items: end;
}

.grid2 {
  max-width: 41% !important;
  display: flex;
  justify-content: left;
}

.edit {
  background-color: white !important;
  border-radius: 50px;
  height: 60px;
  width: 269px;
  font-size: 16px;
  font-weight: 500;

  @media screen and (max-width: 776px) {
    width: 360px;
  }

  @media screen and (max-width: 600px) {
    width: 290px;
  }

  @media screen and (max-width: 422px) {
    width: 290px;
  }

  @media screen and (max-width: 380px) {
    width: 310px;
  }

  @media screen and (max-width: 280px) {
    width: 210px;
  }
}

.root {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: #111116;
}

.text {
  font-size: 35px;
  font-weight: 510;
  color: white;
}

.subtext {
  font-size: 16px;
  font-weight: 400;
  color: white;
}

.maintext {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 776px) {
  .form {
    margin-top: 10px;
    flex-direction: column;
  }

  .margin {
    width: 360px;
  }

  @media screen and (max-width: 600px) {
    .maintext {
      align-items: center;
      text-align: center;
      justify-content: center;
    }

    .form {
      margin-top: 10px;
      flex-direction: column;
      padding-bottom: 1em;
    }

    .margin {
      width: 290px;
    }

    .text {
      font-size: 20px;
      padding-top: 1em;
    }

    .subtext {
      font-size: 12px;
    }
  }

  @media screen and (max-width: 422px) {
    .maintext {
      align-items: center;
      text-align: center;
      justify-content: center;
    }

    .form {
      margin-top: 10px;
      flex-direction: column;
      padding-bottom: 2em;
    }

    .margin {
      width: 290px;
    }

    .text {
      font-size: 20px;
    }

    .subtext {
      font-size: 12px;
    }
  }

  @media screen and (max-width: 380px) {
    .text {
      font-size: 20px;
    }

    .subtext {
      font-size: 12px;
    }

    .form {
      margin-top: 10px;
      flex-direction: column;
    }

    .margin {
      width: 310px;
    }
  }

  @media screen and (max-width: 280px) {
    .text {
      font-size: 20px;
    }

    .subtext {
      font-size: 12px;
    }

    .form {
      margin-top: 10px;
      flex-direction: column;
    }

    .margin {
      width: 210px;
    }
  }

  .grid {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .grid2 {
    max-width: 41% !important;
    display: flex;
    justify-content: end;
  }

  .text {
    text-align: center;
  }

  .subtext {
    text-align: center;
  }
}

@media screen and (max-width: 2150px) {
  .grid2 {
    max-width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .grid {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .root {
    height: 250px;
  }
}

@media screen and (max-width: 600px) {
  .grid2 {
    max-width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
  }

  .grid {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 380px) {
  .grid2 {
    max-width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;

    .text {
      font-size: 10px;
      padding-top: 3em;
    }
  }

  .grid {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 290px) {
  .grid2 {
    max-width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    padding-top: 10px;
  }

  .grid {
    // padding: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
    margin-bottom: 170px;
  }

  .text {
    padding-top: 3em;
  }
}